"use client"

import { PinType } from '@/app/travel/page'
import { cn } from '@/lib/utils'

interface SidebarProps {
  pins: PinType[]
  onSelectPin: (pin: PinType) => void
}

export default function Sidebar({ pins, onSelectPin }: SidebarProps) {
  const formatDate = (dateString: string) => {
    try {
      const date = new Date(dateString)
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      })
    } catch {
      return dateString
    }
  }

  return (
    <div className="h-full bg-card rounded-lg border shadow-sm">
      <div className="p-4 border-b">
        <h2 className="text-xl font-semibold text-card-foreground">Travel Pins</h2>
        <p className="text-sm text-muted-foreground">
          {pins.length} {pins.length === 1 ? 'destination' : 'destinations'}
        </p>
      </div>

      <div className="h-[calc(100%-80px)] overflow-y-auto">
        {pins.length === 0 ? (
          <div className="flex flex-col items-center justify-center h-full p-6 text-center">
            <div className="w-16 h-16 bg-muted rounded-full flex items-center justify-center mb-4">
              <div className="w-8 h-8 bg-muted-foreground/20 rounded-full" />
            </div>
            <h3 className="text-lg font-medium text-card-foreground mb-2">No pins yet</h3>
            <p className="text-sm text-muted-foreground">
              Click on the map to add your first travel destination
            </p>
          </div>
        ) : (
          <div className="p-2">
            {pins.map((pin, index) => (
              <div
                key={pin.id}
                className={cn(
                  "p-4 rounded-lg border mb-2 cursor-pointer transition-all duration-200",
                  "hover:bg-accent hover:border-accent-foreground/20",
                  "focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2"
                )}
                onClick={() => onSelectPin(pin)}
                tabIndex={0}
                role="button"
                onKeyDown={(e) => {
                  if (e.key === 'Enter' || e.key === ' ') {
                    e.preventDefault()
                    onSelectPin(pin)
                  }
                }}
              >
                <div className="flex items-start justify-between mb-2">
                  <h3 className="font-medium text-card-foreground line-clamp-1">
                    {pin.title}
                  </h3>
                  <span className="text-xs text-muted-foreground ml-2 flex-shrink-0">
                    #{index + 1}
                  </span>
                </div>
                
                {pin.description && (
                  <p className="text-sm text-muted-foreground mb-2 line-clamp-2">
                    {pin.description}
                  </p>
                )}
                
                <div className="flex items-center justify-between">
                  <span className="text-xs text-muted-foreground">
                    {formatDate(pin.date)}
                  </span>
                  <div className="flex items-center space-x-1">
                    <div className="w-2 h-2 bg-primary rounded-full" />
                    <span className="text-xs text-muted-foreground">
                      {pin.x.toFixed(1)}%, {pin.y.toFixed(1)}%
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {pins.length > 0 && (
        <div className="p-4 border-t bg-muted/50">
          <div className="text-center">
            <p className="text-xs text-muted-foreground">
              Click any pin to view or edit details
            </p>
          </div>
        </div>
      )}
    </div>
  )
}
