"use client"

import { useState } from 'react'
import Map from '@/components/Map'
import Sidebar from '@/components/Sidebar'
import PinModal from '@/components/PinModal'

export interface PinType {
  id: string
  title: string
  description: string
  date: string
  x: number
  y: number
}

export default function TravelPage() {
  const [pins, setPins] = useState<PinType[]>([])
  const [modalOpen, setModalOpen] = useState(false)
  const [currentPinCoords, setCurrentPinCoords] = useState<{ x: number; y: number } | null>(null)
  const [selectedPin, setSelectedPin] = useState<PinType | null>(null)

  const handleMapClick = (coords: { x: number; y: number }) => {
    setCurrentPinCoords(coords)
    setSelectedPin(null)
    setModalOpen(true)
  }

  const handlePinClick = (pin: PinType) => {
    setSelectedPin(pin)
    setCurrentPinCoords({ x: pin.x, y: pin.y })
    setModalOpen(true)
  }

  const handleModalSubmit = (data: { title: string; description: string; date: string }) => {
    try {
      if (!currentPinCoords) return

      if (selectedPin) {
        // Edit existing pin
        setPins(prev => prev.map(pin => 
          pin.id === selectedPin.id 
            ? { ...pin, ...data }
            : pin
        ))
      } else {
        // Add new pin
        const newPin: PinType = {
          id: Date.now().toString(),
          ...data,
          x: currentPinCoords.x,
          y: currentPinCoords.y
        }
        setPins(prev => [...prev, newPin])
      }

      setModalOpen(false)
      setCurrentPinCoords(null)
      setSelectedPin(null)
    } catch (error) {
      console.error('Error saving pin:', error)
    }
  }

  const handleModalClose = () => {
    setModalOpen(false)
    setCurrentPinCoords(null)
    setSelectedPin(null)
  }

  const handleSelectPin = (pin: PinType) => {
    // Future: Could highlight pin on map or show details
    console.log('Selected pin:', pin)
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto p-4">
        <div className="mb-6">
          <h1 className="text-4xl font-bold text-foreground mb-2">Travel Pin Map</h1>
          <p className="text-muted-foreground">Click on the map to add pins for places you've visited or want to visit</p>
        </div>
        
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6 h-[calc(100vh-200px)]">
          {/* Map Section */}
          <div className="lg:col-span-3">
            <Map 
              pins={pins}
              onMapClick={handleMapClick}
              onPinClick={handlePinClick}
            />
          </div>
          
          {/* Sidebar Section */}
          <div className="lg:col-span-1">
            <Sidebar 
              pins={pins}
              onSelectPin={handleSelectPin}
            />
          </div>
        </div>
      </div>

      {/* Pin Modal */}
      <PinModal
        isOpen={modalOpen}
        onClose={handleModalClose}
        onSubmit={handleModalSubmit}
        initialData={selectedPin ? {
          title: selectedPin.title,
          description: selectedPin.description,
          date: selectedPin.date
        } : undefined}
        isEditing={!!selectedPin}
      />
    </div>
  )
}
