"use client"

import { useRef } from 'react'
import Image from 'next/image'
import { PinType } from '@/app/travel/page'

interface MapProps {
  pins: PinType[]
  onMapClick: (coords: { x: number; y: number }) => void
  onPinClick: (pin: PinType) => void
}

export default function Map({ pins, onMapClick, onPinClick }: MapProps) {
  const mapRef = useRef<HTMLDivElement>(null)

  const handleMapClick = (event: React.MouseEvent<HTMLDivElement>) => {
    if (!mapRef.current) return

    const rect = mapRef.current.getBoundingClientRect()
    const x = ((event.clientX - rect.left) / rect.width) * 100
    const y = ((event.clientY - rect.top) / rect.height) * 100

    onMapClick({ x, y })
  }

  const handlePinClick = (event: React.MouseEvent, pin: PinType) => {
    event.stopPropagation()
    onPinClick(pin)
  }

  return (
    <div className="h-full bg-card rounded-lg border shadow-sm overflow-hidden">
      <div className="p-4 border-b">
        <h2 className="text-xl font-semibold text-card-foreground">World Map</h2>
        <p className="text-sm text-muted-foreground">Click anywhere to add a travel pin</p>
      </div>
      
      <div 
        ref={mapRef}
        className="relative w-full h-[calc(100%-80px)] cursor-crosshair bg-gradient-to-br from-blue-50 to-green-50 dark:from-blue-950 dark:to-green-950"
        onClick={handleMapClick}
      >
        {/* World Map Background */}
        <div className="absolute inset-0 flex items-center justify-center">
          <Image
            src="https://images.pexels.com/photos/335393/pexels-photo-335393.jpeg"
            alt="World Map"
            fill
            className="object-cover opacity-80"
            priority
          />
        </div>

        {/* Overlay for better contrast */}
        <div className="absolute inset-0 bg-background/10" />

        {/* Travel Pins */}
        {pins.map((pin) => (
          <div
            key={pin.id}
            className="absolute transform -translate-x-1/2 -translate-y-1/2 cursor-pointer group"
            style={{
              left: `${pin.x}%`,
              top: `${pin.y}%`,
            }}
            onClick={(e) => handlePinClick(e, pin)}
          >
            {/* Pin Marker */}
            <div className="relative">
              <div className="w-6 h-6 bg-primary rounded-full border-2 border-primary-foreground shadow-lg transition-all duration-200 group-hover:scale-125 group-hover:shadow-xl">
                <div className="w-2 h-2 bg-primary-foreground rounded-full absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2" />
              </div>
              
              {/* Pin Tooltip */}
              <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 opacity-0 group-hover:opacity-100 transition-opacity duration-200 pointer-events-none">
                <div className="bg-popover text-popover-foreground px-3 py-2 rounded-md shadow-lg border text-sm whitespace-nowrap">
                  <div className="font-medium">{pin.title}</div>
                  <div className="text-xs text-muted-foreground">{pin.date}</div>
                </div>
                {/* Tooltip Arrow */}
                <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-popover" />
              </div>
            </div>
          </div>
        ))}

        {/* Instructions overlay when no pins */}
        {pins.length === 0 && (
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="bg-card/90 backdrop-blur-sm rounded-lg p-8 text-center border shadow-lg">
              <h3 className="text-lg font-semibold text-card-foreground mb-2">Start Your Journey</h3>
              <p className="text-muted-foreground">Click anywhere on the map to add your first travel pin</p>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
